<template>
  <div>
    <div class="formDiv">
      <el-form :inline="true" class="demo-form-inline">
        <!-- 查询条件 -->

        <el-form-item label="时间">
          <el-date-picker
            v-model="listQuery.daterangeArr"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="yyyy-MM-dd"
            format="yyyy-MM-dd"
            @change="daterange_change"
          >
          </el-date-picker>
        </el-form-item>

        <el-form-item style="margin-left: 20px !important">
          <el-button type="primary" size="medium" @click="exportBtn"
            >导出</el-button
          >
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { baseManagement } from "@/request/api";
import { formatDate } from "@/assets/libs/unit";
export default {
  data() {
    return {
      listQuery: {
        daterangeArr: [],
        start_time: "",
        end_time: "",
      },
      listQueryBase: {
        daterangeArr: [],
        start_time: "",
        end_time: "",
      },
    };
  },
  methods: {
    daterange_change() {
      if (this.listQuery.daterangeArr.length > 0) {
        this.listQuery.start_time =
          this.listQuery.daterangeArr[0] + " 00:00:00";
        this.listQuery.end_time = this.listQuery.daterangeArr[1] + " 23:59:59";
      }
    },
    // 导出
    getLogisticFromDataModel(params) {
      baseManagement.getLogisticFromDataModel(params).then((res) => {
        console.log("导出 ", res);
        if (res.code == 0) {

            // window.open("http://10.0.20.103:8000/logistic/Logisticdownloads?files=" + res.file_path)
            window.open("http://10.0.0.235:8000/logistic/Logisticdownloads?files=" + res.file_path)
            
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    exportBtn() {
      if (this.listQuery.daterangeArr.length != 2) {
        this.$message.warning("请选择时间！");
        return false;
      }

      this.getLogisticFromDataModel(this.listQuery);
    },
  },
  mounted() {
  },
};
</script>

